:root {
  --main-ff: "RFDewiExtended", sans-serif;
  --color-dark: #2d3040;
  --color-white: #fff;
  --color-orange: #ef7d00;
  --color-orange-dark: #DA7200;
  --color-orange-light: #ffe9ce;
  --color-purple-light: #f9f9ff;
  --color-comet: #5C6073;
  --color-dark-blue: #2E3458;
  --color-darker-blue: #242945;
  --color-grey: #C6C6C6;
  --color-grey3: #ADB0C3;
}

@font-face {
  font-weight: 400;
  font-family: "RFDewiExtended";
  font-style: normal;
  font-display: swap;
  src: url("../fonts/RFDewiExtended-Regular.woff2") format("woff2"), url("../fonts/RFDewiExtended-Regular.woff") format("woff");
}

@font-face {
  font-weight: 600;
  font-family: "RFDewiExtended";
  font-style: normal;
  font-display: swap;
  src: url("../fonts/RFDewiExtended-Semibold.woff2") format("woff2"), url("../fonts/RFDewiExtended-Semibold.woff") format("woff");
}

@font-face {
  font-weight: 700;
  font-family: "RFDewiExtended";
  font-style: normal;
  font-display: swap;
  src: url("../fonts/RFDewiExtended-Bold.woff2") format("woff2"), url("../fonts/RFDewiExtended-Bold.woff") format("woff");
}

@font-face {
  font-weight: 800;
  font-family: "RFDewiExtended";
  font-style: normal;
  font-display: swap;
  src: url("../fonts/RFDewiExtended-Ultrabold.woff2") format("woff2"), url("../fonts/RFDewiExtended-Ultrabold.woff") format("woff");
}

@font-face {
  font-weight: 900;
  font-family: "RFDewiExtended";
  font-style: normal;
  font-display: swap;
  src: url("../fonts/RFDewiExtended-Black.woff2") format("woff2"), url("../fonts/RFDewiExtended-Black.woff") format("woff");
}

.button-reset {
  padding: 0;
  margin: 0;
  background-color: transparent;
  border: none;
  border-radius: 0;
}

.logo__img {
  display: block;
  width: 30rem;
}

@media screen and (max-width: 767px) {
  .logo__img {
    max-width: 15.4rem;
  }
}

.nav__list {
  display: flex;
  align-items: center;
}

.nav__item {
  margin-right: 2.8rem;
}

.nav__item:last-child {
  margin-right: 0;
}

.nav__link {
  position: relative;
  display: inline-block;
  overflow: hidden;
  font-size: 2rem;
  line-height: 3.8rem;
  transition: color 200ms;
}

.nav__link:hover::before {
  transform: translateX(0);
}

.nav__link::before {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 1px;
  background-color: var(--color-dark);
  transform: translateX(-100%);
  transition: transform 200ms;
  content: "";
}

.nav__link.is-current::before {
  transform: translateX(0);
}

.button-burger {
  position: relative;
  z-index: 15;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 6.5rem;
  height: 6.5rem;
  cursor: pointer;
}

.button-burger span {
  display: block;
  margin-bottom: 1rem;
  width: 5.7rem;
  height: 2px;
  background-color: var(--color-dark);
  transition: background-color 200ms, transform 200ms;
}

.button-burger span:last-child {
  margin-bottom: 0;
}

.button-burger:not(.is-active):hover span:nth-child(1) {
  transform: translateY(0.3rem);
}

.button-burger:not(.is-active):hover span:nth-child(2) {
  transform: translateY(-0.3rem);
}

.button-burger:active {
  transform: scale(0.99);
}

.button-burger.is-active span {
  background-color: var(--color-white);
  margin: 0;
}

.button-burger.is-active span:nth-child(1) {
  transform: rotate(32deg);
}

.button-burger.is-active span:nth-child(2) {
  transform: rotate(-32deg);
}

.tel__link {
  display: inline-block;
  font-weight: 800;
  font-size: 2.2rem;
  line-height: 3.6rem;
  transition: color 200ms;
}

.tel__link:hover {
  color: var(--color-orange);
}

.tel__text {
  display: block;
  font-size: 1.3rem;
  line-height: 1.6rem;
  color: #959FAB;
}

.section-name {
  display: block;
  font-weight: 600;
  font-size: 2.4rem;
  line-height: 2.9rem;
  color: rgba(45, 48, 64, 0.4);
}

@media screen and (max-width: 1199px) {
  .section-name {
    font-size: 2rem;
    line-height: 2.4rem;
  }
}

@media screen and (max-width: 767px) {
  .section-name {
    font-size: 1.4rem;
    line-height: 1.6rem;
  }
}

.section-name2 {
  display: block;
  font-weight: 600;
  font-size: 1.8rem;
  line-height: 2.2rem;
  color: rgba(45, 48, 64, 0.4);
}

@media screen and (max-width: 767px) {
  .section-name2 {
    font-size: 1.4rem;
    line-height: 1.6rem;
  }
}

.site-first-title {
  font-weight: 800;
  font-size: 4.2rem;
  line-height: 4.9rem;
  letter-spacing: 0.02em;
}

@media screen and (max-width: 767px) {
  .site-first-title {
    font-size: 2.5rem;
    line-height: 3rem;
  }
}

.site-second-title {
  font-weight: 800;
  font-size: 3.2rem;
  line-height: 3.8rem;
}

@media screen and (max-width: 1199px) {
  .site-second-title {
    font-size: 2.6rem;
    line-height: 3rem;
  }
}

@media screen and (max-width: 767px) {
  .site-second-title {
    font-size: 2.5rem;
    line-height: 3rem;
  }
}

.slider-nav {
  width: 10.8rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

@media screen and (max-width: 767px) {
  .slider-nav {
    width: 7.8rem;
  }
}

.slider-nav.hero__slider-nav .progress {
  transition: fill 200ms;
}

.slider-nav.hero__slider-nav .progress:nth-child(1) {
  stroke-dasharray: 135;
  stroke-dashoffset: 135;
}

.slider-nav.js-anim-start .progress:nth-child(1) {
  animation: circle 9s linear infinite;
  animation-delay: 0.1s;
}

.slider-nav__circle {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.slider-nav__circle .progress {
  transition: stroke 200ms;
}

.slider-nav__circle .progress:nth-child(1) {
  stroke-dasharray: 135;
  stroke-dashoffset: 0;
}

@keyframes circle {
  from {
    stroke-dashoffset: 135;
  }
  to {
    stroke-dashoffset: 0;
  }
}

.slider-nav__btn {
  position: relative;
  width: 4.4rem;
  height: 4.4rem;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  border: 1px solid transparent;
  cursor: pointer;
  transition: background-color 200ms, border-color 200ms;
}

.slider-nav__btn:hover .progress:nth-child(1) {
  stroke: var(--color-orange);
}

.slider-nav__btn:hover .slider-nav__btn-icon {
  fill: var(--color-orange);
}

.slider-nav__btn:active {
  transform: scale(0.9);
}

.slider-nav__btn.swiper-button-disabled {
  opacity: 0.5;
  pointer-events: none;
  border-color: rgba(45, 48, 64, 0.1);
}

.slider-nav__btn.swiper-button-disabled .slider-nav__circle {
  opacity: 0;
}

.slider-nav__btn-icon {
  display: block;
  width: 2rem;
  height: 2rem;
  fill: var(--color-dark);
  transition: fill 200ms;
}

@media screen and (max-width: 767px) {
  .slider-nav__btn {
    width: 3.5rem;
    height: 3.5rem;
  }
  .slider-nav__btn-icon {
    width: 1.5rem;
    height: 1.5rem;
  }
}

.button {
  position: relative;
  z-index: 1;
  display: inline-block;
  padding: 2.3rem 4.3rem;
  border-radius: 1.5rem;
  border: 2px solid var(--color-orange);
  color: var(--color-orange);
  font-weight: 900;
  font-size: 1.5rem;
  line-height: 2rem;
  text-align: center;
  letter-spacing: 0.11em;
  text-transform: uppercase;
  transition: color 400ms;
  overflow: hidden;
  cursor: pointer;
}

.button::before {
  position: absolute;
  top: 100%;
  left: 50%;
  z-index: -1;
  width: 7rem;
  height: 7rem;
  background-color: var(--color-orange);
  border-radius: 50%;
  transform: translate(-50%);
  transition: all 400ms;
  content: "";
}

.button_secondary {
  background-color: var(--color-orange);
  color: var(--color-white);
  text-transform: none;
}

.button_secondary::before {
  background-color: var(--color-orange-dark);
}

@media screen and (max-width: 767px) {
  .button {
    font-size: 1rem;
    line-height: 1.5rem;
    padding: 1.9rem 3.4rem;
  }
}

@media screen and (min-width: 767px) {
  .button:hover {
    color: var(--color-white);
  }
  .button:hover::before {
    width: 55rem;
    height: 55rem;
    top: 50%;
    transform: translate(-50%, -50%);
  }
}

.button:active {
  transform: scale(0.99);
}

.preloader {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: var(--color-white);
  z-index: 19;
  transform: opacity 200ms, visibillty 200ms;
}

.preloader._is-loaded {
  opacity: 0;
  visibility: hidden;
}

.preloader__image {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 7rem;
  height: 7rem;
  transform: translate(-50%, -50%);
  animation: preloader-rotate 3s infinite linear;
  color: var(--color-orange);
}

@keyframes preloader-rotate {
  100% {
    transform: translate(-50%, -50%) rotate(360deg);
  }
}

.btn-top-container {
  position: fixed;
  left: 0;
  bottom: 4rem;
  width: 100%;
  opacity: 0;
  visibility: hidden;
  transition: opacity 200ms, visibility 200ms;
  pointer-events: none;
}

.btn-top-container .btn-top {
  margin-left: auto;
  pointer-events: auto;
}

.btn-top-container.is-active {
  opacity: 1;
  visibility: visible;
}

@media screen and (max-width: 1199px) {
  .btn-top-container {
    display: none;
  }
}

.btn-top {
  display: flex;
  align-items: center;
  cursor: pointer;
}

.btn-top:hover ._icon-wrap {
  background-color: var(--color-orange);
}

.btn-top:hover ._icon-wrap .footer__btn-top-icon {
  fill: var(--color-orange-light);
}

.btn-top:hover ._name {
  color: var(--color-orange-light);
}

.btn-top ._name {
  font-weight: 700;
  color: var(--color-orange);
  font-size: 1.2rem;
  line-height: 1.6rem;
  text-transform: uppercase;
  transition: color 200ms;
}

.btn-top ._icon-wrap {
  width: 5.8rem;
  height: 5.8rem;
  background-color: var(--color-orange-light);
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-right: 1.5rem;
  transition: background-color 200ms;
}

.btn-top-icon {
  width: 2.7rem;
  height: 2.7rem;
  fill: var(--color-orange);
  transition: fill 200ms;
}

.markweber {
  display: block !important;
}

.markweber img {
  transform: translateY(0.2rem);
}

.grid-container {
  display: grid;
  grid-template-columns: repeat(16, 1fr);
  gap: 20px;
}

@media screen and (max-width: 991px) {
  .grid-container {
    gap: 0;
  }
}

.grid-container__block:nth-child(1) {
  grid-column: 4 span;
}

@media screen and (max-width: 991px) {
  .grid-container__block:nth-child(1) {
    grid-column: 16 span;
  }
}

.grid-container__block:nth-child(2) {
  grid-column: 12 span;
}

@media screen and (max-width: 991px) {
  .grid-container__block:nth-child(2) {
    grid-column: 16 span;
  }
}

.file {
  display: inline-flex;
  align-items: center;
  margin-bottom: 3rem;
}

@media screen and (max-width: 767px) {
  .file {
    align-items: flex-start;
    margin-bottom: 1.5rem;
  }
}

.file:nth-last-of-type(1) {
  margin-bottom: 0;
}

.file__icon-wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 3rem;
  height: 3rem;
  background-color: var(--color-purple-light);
  border-radius: 50%;
  margin-right: 1.6rem;
}

@media screen and (max-width: 767px) {
  .file__icon-wrapper {
    width: 2rem;
    height: 2rem;
    margin-right: 1rem;
  }
}

.file__icon {
  width: 1.6rem;
  height: 1.6rem;
  fill: none;
  stroke: var(--color-orange);
}

.file__text {
  display: block;
  max-width: 92.4rem;
  font-size: 1.6rem;
  line-height: 2.4rem;
  text-decoration: underline;
}

@media screen and (max-width: 767px) {
  .file__text {
    font-weight: 600;
    font-size: 1.3rem;
    line-height: 1.8rem;
  }
}

.file:hover .file__text {
  text-decoration: none;
}

.drop-menu-container {
  max-height: 0;
  transition: max-height 200ms;
  overflow: hidden;
}

.button-search {
  width: 3rem;
  height: 3rem;
  display: flex;
  align-items: center;
  justify-self: center;
  cursor: pointer;
  margin-left: 4rem;
}

.button-search:hover .icon {
  fill: var(--color-orange);
}

.button-search__icon {
  display: block;
  width: 100%;
  height: 100%;
  fill: var(--color-dark);
}

.switch {
  display: block;
  cursor: pointer;
  display: flex;
  align-items: center;
}

@media screen and (max-width: 991px) {
  .switch {
    display: inline-flex;
  }
}

.switch__container {
  display: block;
  position: relative;
  width: 3.6rem;
  height: 2rem;
  background-color: var(--color-orange);
  border-radius: 4rem;
  margin: 0 0.8rem;
}

.switch__container::before {
  position: absolute;
  top: 50%;
  left: 0.3rem;
  width: 1.4rem;
  height: 1.4rem;
  border-radius: 50%;
  background-color: var(--color-white);
  transform: translateY(-50%);
  transition: left 200ms;
  content: "";
}

.switch__name {
  font-weight: 700;
  font-size: 1.4rem;
  line-height: 1.6rem;
  text-transform: uppercase;
  transition: color 200ms;
}

.switch__name._left {
  color: var(--color-orange);
}

._text-grey {
  color: var(--color-grey3);
}

.disable-scroll {
  overflow: hidden;
  height: 100vh;
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
}

.highlight {
  background: #FDF2E6 !important;
}

.file-upload {
  cursor: pointer;
  margin-left: auto;
  display: inline-block;
}

.file-upload__text {
  display: flex;
  align-items: center;
  font-weight: 700;
  font-size: 1.2rem;
  line-height: 1.6rem;
  text-transform: uppercase;
  text-decoration: none;
}

.file-upload__text-icon {
  width: 2.4rem;
  height: 2.4rem;
  fill: var(--color-orange);
  margin-right: 0.4rem;
}

.file-upload__type {
  display: block;
  font-size: 1.6rem;
  line-height: 2rem;
  color: var(--color-grey3);
}

@media screen and (max-width: 767px) {
  .file-upload__type {
    padding-left: 2.7rem;
  }
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

html {
  font-size: 10px;
  line-height: 1.4;
  text-rendering: optimizeLegibility;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

@media screen and (min-width: 2100px) {
  html {
    font-size: 14px;
  }
}

@media screen and (max-width: 1536px) {
  html {
    font-size: 8px;
  }
}

@media screen and (max-width: 1366px) {
  html {
    font-size: 7px;
  }
}

@media screen and (max-width: 767px) {
  html {
    font-size: 10px;
  }
}

@media screen and (max-width: 360px) {
  html {
    font-size: 9px;
  }
}

@media screen and (max-width: 320px) {
  html {
    font-size: 8px;
  }
}

body {
  color: var(--color-dark);
  font-family: var(--main-ff);
  background-color: var(--color-white);
  font-size: 1.6rem;
  line-height: 1.9rem;
  text-rendering: optimizeLegibility;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

@media screen and (max-width: 767px) {
  body {
    font-size: 1.4rem;
    line-height: 1.8rem;
  }
}

img {
  max-width: 100%;
  height: auto;
  vertical-align: middle;
}

a {
  color: inherit;
  text-decoration: none;
}

.list-reset {
  padding: 0;
  margin: 0;
  list-style: none;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  clip: rect(0 0 0 0);
}

.site-container {
  width: 100%;
  max-width: 1680px;
  margin: 0 auto;
  padding: 0 32px;
}

@media screen and (min-width: 2100px) {
  .site-container {
    max-width: 100%;
  }
}

@media screen and (max-width: 767px) {
  .site-container {
    padding: 0 20px;
  }
}

.page-wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  overflow: hidden;
}

.page-main {
  flex-grow: 1;
}

.header {
  padding-top: 2.4rem;
}

@media screen and (max-width: 767px) {
  .header {
    padding-top: 2.2rem;
  }
}

.header__container {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.header__logo {
  margin-right: 10.9rem;
}

@media screen and (max-width: 767px) {
  .header__logo {
    margin-right: 2rem;
  }
}

.header__nav {
  margin-right: 9.9rem;
}

@media screen and (max-width: 1199px) {
  .header__nav {
    display: none;
  }
}

@media screen and (max-width: 1199px) {
  .header__btn-burger {
    margin-left: 3rem;
    flex-shrink: 0;
  }
}

@media screen and (max-width: 767px) {
  .header__btn-burger {
    width: 4rem;
    height: 4rem;
  }
  .header__btn-burger span {
    width: 100%;
  }
}

.header__links {
  margin-left: auto;
}

@media screen and (max-width: 1199px) {
  .header__links {
    display: none;
  }
}

@media screen and (max-width: 1199px) {
  .header__btn-search {
    display: none;
  }
}

.header__btn-tel {
  margin-left: auto;
  width: 3rem;
  height: 3rem;
  display: flex;
  align-items: center;
  justify-content: center;
}

@media screen and (min-width: 1199px) {
  .header__btn-tel {
    display: none;
  }
}

.header__btn-tel-icon {
  width: 2.6rem;
  height: 2.6rem;
}

.footer {
  color: var(--color-white);
}

.footer__logo .logo__img {
  width: 21.9rem;
}

.footer__content-text p {
  font-weight: 700;
  font-size: 2.4rem;
  line-height: 2.9rem;
  max-width: 42.7rem;
}

@media screen and (max-width: 767px) {
  .footer__content-text p {
    font-size: 1.6rem;
    line-height: 1.9rem;
  }
}

.footer__tel .tel__link {
  font-size: 4.2rem;
  line-height: 4.9rem;
  letter-spacing: 0.02em;
  color: var(--color-white);
  margin-bottom: 0.9rem;
}

.footer__tel .tel__link:hover {
  color: var(--color-orange);
}

@media screen and (max-width: 767px) {
  .footer__tel .tel__link {
    font-size: 2.5rem;
    line-height: 3rem;
    letter-spacing: 0.02em;
    margin-bottom: 0.4rem;
  }
}

.footer__tel .tel__text {
  font-size: 1.6rem;
  line-height: 1.9rem;
}

@media screen and (max-width: 767px) {
  .footer__tel .tel__text {
    font-size: 1.4rem;
    line-height: 1.8rem;
  }
}

.footer__mail-link {
  position: relative;
  display: inline-block;
  font-weight: 800;
  font-size: 4.2rem;
  line-height: 4.9rem;
  letter-spacing: 0.02em;
  color: var(--color-white);
  transition: color 200ms;
}

.footer__mail-link:hover {
  color: var(--color-orange);
}

.footer__mail-link:hover::before {
  opacity: 0;
}

.footer__mail-link::before {
  position: absolute;
  left: 0;
  bottom: -1.1rem;
  width: 100%;
  height: 1px;
  background-color: var(--color-white);
  opacity: 0.3;
  transition: opacity 200ms;
  content: "";
}

@media screen and (max-width: 767px) {
  .footer__mail-link::before {
    bottom: -0.3rem;
  }
}

@media screen and (max-width: 767px) {
  .footer__mail-link {
    font-size: 2.5rem;
    line-height: 3rem;
    letter-spacing: 0.02em;
  }
}

